@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.hidden,
.is-hidden {
  display: none !important;
}

.card {
  position: relative;
  display: flex;
  flex-flow: column;
  justify-content: flex-end;
  color: #fdf2f2;
  padding: 1.2rem 0.8rem;
  border: 0;
  overflow: hidden;
  box-shadow: 0 0.4rem 0.8rem rgba(28, 19, 18, 0);
  background-color: #1c1312;
  min-height: 22vw;
  padding-top: 11.2rem;
  text-align: left;
  z-index: 0;
}
@media only screen and (min-width: 1200px) {
  .card {
    padding-top: 12.8rem;
  }
}

.card__background {
  z-index: 1;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  opacity: 0.8;
  transition: transform 0.19s ease;
}
.card__background::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(to bottom, rgba(28, 19, 18, 0.24) 0%, rgba(28, 19, 18, 0.7) 50%, rgba(28, 19, 18, 0.9) 100%);
  z-index: 2;
}
.card__background img {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.card__tags {
  position: absolute;
  top: 0;
  left: 0;
  padding: 0.8rem;
  font-size: 0.512rem;
  color: #fdf2f1;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  display: flex;
  flex-wrap: wrap;
  z-index: 1;
  opacity: 0.9;
  pointer-events: none;
}

.card__tag {
  background-color: #1c1312;
  border: 1px solid rgba(205, 154, 152, 0.62);
  border-radius: 0.2rem;
  padding: 0 0.8rem;
}

.card__body {
  position: relative;
  z-index: 2;
}
.card__body > :not(:last-child) {
  margin-bottom: 0.8rem;
}

.card__title,
.card__description {
  text-shadow: 0 1px 0.38em rgba(28, 19, 18, 0.5);
}

.card__title {
  color: #fdf2f1;
  font-weight: 700;
  line-height: 1.6rem;
  transition: color 0.19s ease;
}

.card__keywords,
.card__description {
  transition: opacity 0.19s ease;
  will-change: opacity;
}

.card__keywords {
  font-size: 0.6875rem;
  line-height: 1.2rem;
  opacity: 0.8;
}
.card__keywords .keyword {
  border-bottom: 1px dotted currentColor;
  margin-right: 0.4rem;
}

.card__description {
  font-size: 0.8rem;
  line-height: 1.2rem;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 4;
  overflow: hidden;
  text-overflow: ellipsis;
}

.card__controls {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  border-top: 1px solid rgba(205, 154, 152, 0.38);
  padding-top: 0.8rem;
}
.card__start {
  border: 1px solid hsl(2deg, 83.1932773109%, 94.05%);
  color: currentColor;
  margin-bottom: 0;
}
.card__start:hover {
  border-bottom-color: hsl(2deg, 83.1932773109%, 94.05%);
  background-color: hsl(2deg, 83.1932773109%, 94.05%);
  color: #1c1312;
}

.card__pdf {
  color: #cd9a98;
  font-size: 0.6875rem;
}

.section--catalog {
  background-color: #1b1313;
  box-shadow: 0 0 4.8rem rgba(27, 19, 19, 0.38);
  color: hsl(2deg, 83.1932773109%, 94.05%);
  text-align: center;
}
.section--catalog .section__inner {
  width: 100%;
  max-width: initial;
  padding: 3.2rem 2px 2px;
}
.section--catalog .section__item--title {
  margin-bottom: 1.6rem;
}
.section--catalog .section__item--title span {
  color: #cd9a98;
  font-size: 0.8rem;
  letter-spacing: 0.05em;
  margin-right: 0.4rem;
  text-transform: uppercase;
}

.section__item--grid {
  padding-top: 1.6rem;
  display: grid;
  grid-gap: 2px;
  grid-template-columns: repeat(auto-fit, minmax(17.6rem, 1fr));
}
@media only screen and (min-width: 1200px) {
  .section__item--grid {
    grid-template-columns: repeat(auto-fit, minmax(19.2rem, 1fr));
  }
}