.card__background {
  z-index: 1;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  opacity: 0.8;
  transition: transform $duration-short $easing-base;


  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(
      to bottom,
      rgba($text-color, 0.24) 0%,
      rgba($text-color, 0.7) 50%,
      // rgba($text-color, 1) 90%
      rgba($text-color, .9) 100%
    );
    z-index: 2;
  }

  img {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.card__tags {
  position: absolute;
  top: 0;
  left: 0;
  padding: $base-unit * .5;
  font-size: ms(-3);
  color: $color-bg-1;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  display: flex;
  flex-wrap: wrap;
  z-index: 1;
  opacity: .9;
  pointer-events: none;
}

.card__tag {
  background-color: $text-color;
  border: 1px solid rgba($color-bg--2, .62);
  border-radius: $base-unit * .125;
  padding: 0 $base-unit * .5;
}

.card__body {
  position: relative;
  z-index: 2;

  > :not(:last-child) {
    margin-bottom: $base-unit * .5;
  }
}

.card__title,
.card__description {
  text-shadow: 0 1px 0.38em rgba($text-color, 0.5);
}

.card__title {
  color: $color-bg-1;
  font-weight: 700;
  line-height: $line-height-0;
  transition: color $duration-short $easing-base;
}

.card__keywords,
.card__description {
  transition: opacity $duration-short $easing-base;
  will-change: opacity;
}

.card__keywords {
  font-size: $font-size-sans--2;
  line-height: $line-height--2;
  // margin-bottom: $base-unit * .5;
  opacity: 0.8;

  .keyword {
    border-bottom: 1px dotted currentColor;
    margin-right: $base-unit * 0.25;
  }
}

.card__description {
  // opacity: 0.8;
  font-size: $font-size-sans--1;
  line-height: $line-height--2;
  // https://developer.mozilla.org/en-US/docs/Web/CSS/-webkit-line-clamp
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 4;
  overflow: hidden;
  text-overflow: ellipsis;
}

.card__controls {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  border-top: 1px solid rgba($color-bg--2, .38);
  padding-top: $base-unit * .5;

  > * {
    // flex: 1 0;
  }
}

