/* ==================================================
/* External dependecies
/* ================================================== */

// https://github.com/paranoida/sass-mediaqueries
@import '../../node_modules/sass-mediaqueries/media-queries';

// https://github.com/necolas/normalize.css/
@import '../../node_modules/normalize.css/normalize';

@import 'tools/vendor/vendor';
@import 'tools/libs/libs';

@import 'variables/variables';

@import "card";

.section--catalog {
  background-color: $color-text;
  box-shadow: 0 0 $base-unit * 3 rgba($color-text, 0.38);
  color: $bg-color;
  text-align: center;

  .section__inner {
    width: 100%;
    max-width: initial;
    padding: $base-unit * 2 2px 2px;
  }

  .section__item--title {
    margin-bottom: $base-unit;

    span {
      color: $color-bg--2;
      font-size: $font-size-sans--1;
      letter-spacing: 0.05em;
      margin-right: $base-unit * 0.25;
      text-transform: uppercase;
    }
  }
}

.section__item--grid {
  padding-top: $base-unit;
  display: grid;
  grid-gap: 2px;
  // grid-gap: $base-unit * 2;
  grid-template-columns: repeat(auto-fit, minmax($base-unit * 11, 1fr));

  @include min-screen($screen-xl) {
    grid-template-columns: repeat(auto-fit, minmax($base-unit * 12, 1fr));
  }
}
